import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Service } from '../../providers/service';
import { HomePage } from '../home/home';
import { AngularFireDatabase } from 'angularfire2/database';

@IonicPage()
@Component({
  selector: 'page-medication',
  templateUrl: 'medication.html',
})
export class MedicationPage {
  isShowProvider = false;
  isVerified = true;
  isUnverified = false;
  providers = [];
  phone = '';
  
  searchTitle = 'Search Provider';

  constructor(public navCtrl: NavController, public navParams: NavParams, private service: Service, private db: AngularFireDatabase) {
    this.phone = this.navParams.get('provider');
    
    this.db.list('users').subscribe(data => {
      data.map(d => {
        if (d.type !== 'Customer' && d.$key !== localStorage.getItem('userId')) {
          this.providers.push(d);
        }

        if (d.phone === this.phone) {
          this.searchTitle = d.name;
          this.isVerified = d.verified ? true : false;
          this.isUnverified = !d.verified ? true : false;
        }
      });
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AppointmentPage');
  }

  selectProvider(data) {
    if (data.verified) {
      this.isVerified = true;
      this.isUnverified = false;
    } else {
      this.isVerified = false;
      this.isUnverified = true;
    }
    this.searchTitle = data.name;
    this.isShowProvider = false;
  }

  goHome() {
    this.navCtrl.setRoot(HomePage);
  }
}



