import { Component } from '@angular/core';
import { NavController, ModalController, ViewController, NavParams } from 'ionic-angular';
import { AngularFireDatabase } from 'angularfire2/database';
import { Service } from '../../providers/service';

import { AppointmentPage } from '../appointment/appointment';
import { MedicationPage } from '../medication/medication';
import { PaymentPage } from '../payment/payment';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  isShowProvider = false;
  isVerified = false;
  isUnverified = false;
  providers = [];
  
  provider = '';
  message = '';
  messages = [];
  roomId = '';
  searchTitle = 'Search Provider';
  userId = '';
  username = '';
  modalPage: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private service: Service, private db: AngularFireDatabase, private modal: ModalController) {
    this.userId = localStorage.getItem('userId');
    this.service.getUserName(this.userId).then((res: any) => {
      this.username = res;
    });

    this.db.list('users').subscribe(data => {
      data.map(d => {
        if (d.$key !== this.userId) {
          this.providers.push(d);      
        }
      });
    });

    this.modalPage = this.modal.create(ModalPage);
    console.log(localStorage.getItem('phone'));
  }

  selectProvider(data) {
    if (data.verified) {
      this.isVerified = true;
      this.isUnverified = false;
    } else {
      this.isVerified = false;
      this.isUnverified = true;
    }
    this.provider = data.phone;
    this.searchTitle = data.name;
    this.isShowProvider = false;
    this.service.getRoomId(this.userId, this.provider, localStorage.getItem('phone')).then((res: any) => {
      this.roomId = res;
      this.db.list('messages/' + this.roomId + '/messages').subscribe(data => {
        this.messages = data;
      });
    });
  }

  sendMessage() {
    let time = new Date().getTime();
    if (this.message != '') {
      this.service.sendMessage(this.roomId, {userId: this.userId, message: this.message, updatedAt: time}).then(res => {
        console.log(res);
        this.message = '';
      });
    }
  }

  goAppointment() {
    if (this.provider == '') {
      this.modalPage.present();
    } else {
      this.navCtrl.setRoot(AppointmentPage, {provider: this.provider});
    }
  }

  goPayment() {
    if (this.provider == '') {
      this.modalPage.present();
    } else {
      if (this.isVerified) {
        
      } else if (this.isUnverified) {
        this.navCtrl.setRoot(PaymentPage, {provider: this.provider});        
      }
    }
  }

  goMedication() {
    if (this.provider == '') {
      this.modalPage.present();
    } else {
      this.navCtrl.setRoot(MedicationPage, {provider: this.provider});
    }
  }
}

@Component({
  selector: 'page-modal',
  templateUrl: 'modal.html'
})
export class ModalPage {
  constructor(private view: ViewController) {
    
  }

  closeModal() {
    this.view.dismiss();
  }
}


