import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { LoginPage } from '../pages/login/login';
import { SignupPage } from '../pages/signup/signup';
import { HomePage, ModalPage } from '../pages/home/home';
import { AppointmentPage } from '../pages/appointment/appointment';
import { MedicationPage } from '../pages/medication/medication';
import { PaymentPage } from '../pages/payment/payment';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { HttpClientModule } from '@angular/common/http';
import { Service } from '../providers/service';

export const config = {
  apiKey: "AIzaSyAYrfM7rAx-dZovkKtZdQS4KsPBziKle2M",
  authDomain: "healthpass-2fdf1.firebaseapp.com",
  databaseURL: "https://healthpass-2fdf1.firebaseio.com",
  projectId: "healthpass-2fdf1",
  storageBucket: "healthpass-2fdf1.appspot.com",
  messagingSenderId: "329443566792"
};

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    AppointmentPage,
    MedicationPage,
    PaymentPage,
    ModalPage,
    LoginPage,
    SignupPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(config),
    AngularFireDatabaseModule,
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    AppointmentPage,
    MedicationPage,
    PaymentPage,
    ModalPage,
    LoginPage,
    SignupPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    Service
  ]
})
export class AppModule {}



